<template>

    <form-group>

        <slot name="label">
            <form-label v-if="label || hasDefaultSlot" :for="id" v-html="label"/>
        </slot>

        <slot name="control">
            <input
                :id="id"
                :type="type"
                :name="name"
                :value="value"
                :pattern="pattern"
                :readonly="readonly"
                :required="required"
                :maxlength="maxlength"
                :placeholder="placeholder"
                :disabled="disabled || readonly"
                :class="mergeClasses(controlClasses, colorableClasses)"
                :aria-label="label"
                :aria-describedby="id"
                :autocomplete="autocomplete"
                v-bind-events="bindEvents"
                v-on:input="updated($event.target.value)"
            />
        </slot>

        <slot/>

        <slot name="help">
            <help-text v-if="helpText" v-html="helpText" />
        </slot>

        <slot name="feedback">
            <form-feedback v-if="validFeedback" v-html="validFeedback" valid />
            <form-feedback v-if="invalidFeedback" v-html="invalidFeedback" invalid />
        </slot>

    </form-group>

</template>

<script>

import HelpText from '../HelpText';
import FormGroup from '../FormGroup';
import FormLabel from '../FormLabel';
import FormFeedback from '../FormFeedback';
import Colorable from '../../Mixins/Colorable';
import FormControl from '../../Mixins/FormControl';
import MergeClasses from '../../Mixins/MergeClasses';

export default {

    name: 'input-field',

    mixins: [
        Colorable,
        FormControl,
        MergeClasses
    ],

    components: {
        HelpText,
        FormGroup,
        FormLabel,
        FormFeedback
    },

    props: {

        /**
         * The type attribute
         *
         * @property String
         */
        type: {
            type: String,
            default: 'text'
        }

    }

}

</script>
